import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TripComponent} from './trip/trip.component';
import { ArrivalComponent} from './trip/arrival/arrival.component';
import { CarComponent} from './trip/car/car.component';
import { DriverComponent} from './trip/driver/driver.component';

const routes: Routes = [  
{ path: 'trip', component: TripComponent},
{ path: 'driver', component: DriverComponent},
{ path: 'car', component: CarComponent},
{ path: 'arrival', component: ArrivalComponent},
{ path: '', pathMatch: 'full', component: TripComponent },
{ path: '**', redirectTo: '/' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
